import Vue from "vue";
import VueRouter from "vue-router";
import indexView from "../views/index.vue";
import HomeView from "../views/home.vue";
Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "index",
    component: indexView,
  },
  {
    path: "/home",
    name: "home",
    component: HomeView
  },
  {
    path: "/power",
    name: "供电运行方式管理",
    component: HomeView,
    children: [
      {
        path: "powerChart",
        name: "供电运行图",
        component: () => import('@/views/power/powerChart'),
      },
      {
        path: "powerPlan",
        name: "供电运行方案",
        component: () => import('@/views/power/powerPlan'),
      },
      {
        path: "powerDiagram",
        name: "供电运行图详情",
        component: () => import('@/views/power/powerDiagram'),
      },
    ]
  },
  {
    path: "/statistics",
    name: "统计分析管理",
    component: HomeView,
    children: [
      {
        path: "staindex",
        name: "统计分析",
        component: () => import('@/views/statistics/staindex'),
        children: [
          {
            path: "accident",
            name: "事故分析",
            component: () => import('@/views/statistics/staindex/accident'),
          },
          {
            path: "deviveFault",
            name: "设备故障分析",
            component: () => import('@/views/statistics/staindex/deviveFault'),
          },
          {
            path: "trip",
            name: "设备跳闸分析",
            component: () => import('@/views/statistics/staindex/trip'),
          },
          {
            path: "loadValley",
            name: "负荷高峰低谷转移",
            component: () => import('@/views/statistics/staindex/loadValley'),
          },
        ]
      },
      {
        path: "assessment",
        name: "评估管理",
        component: () => import('@/views/statistics/assessment'),
      },
      {
        path: "runState",
        name: "设施设备运行状态",
        component: () => import('@/views/statistics/runState'),
      },
    ]
  },
  {
    path: "/planmanagement",
    name: "调度预案管理",
    component: HomeView,
    children: [{
        path: "plan",
        name: "预案管理",
        component: () => import('@/views/planmanagement/plan'),
      },
      {
        path: "planup",
        name: "预案编制",
        component: () => import('@/views/planmanagement/planup'),
      }]
  },
  {
    path: "/basicmanagement",
    name: "基础信息管理",
    component: HomeView,
    children: [{
        path: "basic",
        name: "变电所基础数据",
        component: () => import('@/views/basicmanagement/basic'),
      },
      {
        path: "basic2",
        name: "继电保护逻辑图",
        component: () => import('@/views/basicmanagement/basic2'),
      },
      {
        path: "basic3",
        name: "接触网基础数据",
        component: () => import('@/views/basicmanagement/basic3'),
      }
    ]
  },
  {
    path: "/devicesmanagement",
    name: "设施设备管理",
    component: HomeView,
    children: [{
        path: "devices",
        name: "设备缺陷管理",
        component: () => import('@/views/devicesmanagement/devices'),
      },
      {
        path: "devices2",
        name: "设备投撤管理",
        component: () => import('@/views/devicesmanagement/devices2'),
      },
      {
        path: "devices3",
        name: "备品备件管理",
        component: () => import('@/views/devicesmanagement/devices3'),
      }
    ]
  },
  {
    path: "/permissionmanagement",
    name: "权限管理",
    component: HomeView,
    children: [
      {
        path: "gwper",
        name: "岗位管理",
        component: () => import('@/views/permissionmanagement/permission'),
        children: [
          {
            path: "gwp",
            name: "岗位配置",
            component: () => import('@/views/permissionmanagement/gwper/gwp'),
          },
          {
            path: "qxp",
            name: "权限配置",
            component: () => import('@/views/permissionmanagement/gwper/qxp'),
          },
        ]
      },
      {
        path: "jsper",
        name: "角色管理",
        component: () => import('@/views/permissionmanagement/jsper'),
      },
      {
        path: "rzper",
        name: "日志管理",
        component: () => import('@/views/permissionmanagement/rzper'),
      },
    ]
  },
  {
    path: "/processmanagement",
    name: "流程管理",
    component: HomeView,
    children: [{
      path: "process",
      component: () => import('@/views/processmanagement/process'),
    }]
  },
];

const router = new VueRouter({
  routes,
  mode: 'history',
});

export default router;
